import axios from 'axios';
import articlesData from './data/articles.json';
import friendsData from './data/friends.json';
import toolsData from './data/tools.json';

// 创建axios实例（保留以便将来可能的远程API调用）
const api = axios.create({
    baseURL: process.env.VUE_APP_API_URL || 'http://localhost:5000', // 默认使用本地开发服务器
    timeout: 5000, // 请求超时时间
    headers: {
        'Content-Type': 'application/json',
    },
});

// 请求拦截器
api.interceptors.request.use(
    (config) => {
        // 在这里可以添加认证token等
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// 响应拦截器
api.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        // 统一处理错误
        console.error('API请求错误:', error);
        return Promise.reject(error);
    }
);

// 从本地JSON文件获取数据的方法
api.getLocalData = (path) => {
    return new Promise((resolve, reject) => {
        let data;

        // 检查是否是获取单篇文章的请求
        const articleMatch = path.match(/^\/api\/articles\/(\d+)$/);
        if (articleMatch) {
            const articleId = parseInt(articleMatch[1]);
            const article = articlesData.find(a => a.id === articleId);
            if (article) {
                data = article;
            } else {
                reject(new Error('Article not found'));
                return;
            }
        } else {
            // 根据路径返回相应的数据
            switch (path) {
                case '/api/articles':
                    data = articlesData;
                    break;
                case '/api/friends':
                    data = friendsData;
                    break;
                case '/api/tools':
                    data = toolsData;
                    break;
                default:
                    data = [];
            }
        }

        // 模拟axios响应格式
        resolve({
            data: data,
            status: 200,
            statusText: 'OK',
            headers: {},
            config: {}
        });
    });
};

// 覆盖原有的get方法，优先使用本地数据
const originalGet = api.get;
api.get = function (url, config) {
    // 检查是否是我们要拦截的API路径
    if (url.startsWith('/api/articles/') || ['/api/articles', '/api/friends', '/api/tools'].includes(url)) {
        return this.getLocalData(url);
    }
    // 其他API调用使用原始axios方法
    return originalGet(url, config);
};

// 添加静态资源基础URL
api.staticBaseUrl = '';

export default api;